.masthead {
    padding: 11em 0;
    background-image: url('https://concepto.de/wp-content/uploads/2015/03/paisaje-2-e1549600987975.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    border-top: solid 2em #B69760;
  }
  
  .masthead-heading,
  .masthead-intro,
  .content-footer {
    text-align: center;
  }

  
  .masthead-intro {
    /* Text Declarations */
    margin-bottom: 0.1em;
    font-family: "Gentium Book Basic," Georgia, serif;
    font-size: 4em;
    color: #e20000;
    text-transform: uppercase;
    letter-spacing: .08em;
  }
  
  
  .masthead-heading {
    /*Layout Declarations */
    margin-top: -0.2em;
    /* Typography Declarations */
    font-family: "Open Sans", "Helvetica Neue", sans-serif;
    font-weight: light;
    font-size: 1.5em;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  
  /* Text Declarations for Sections */
  
  .introduction-section > p,
  .location-section > p,
  .content-footer > p,
  .questions-section > p {
    font-weight: 300;
    letter-spacing: 0.05em;
    text-align: left;
  }
  
  /* Section Styling */
  
  .introduction-section,
  .location-section,
  .questions-section {
    max-width: 38em;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2em;
    text-align: left;
  }
  
  .questions-section > h2 {
    /*Typography Declarations */
    font-family: "Gentium Book Basic", Georgia, serif;
    font-size: 1.1 em;
    font-weight: bold;
    color: #222222;
  }
  
  /* Footer Styling */
  
  .fa-stack fa-lg {
    display: inline-block;
  }
  
  .footer-container > p {
    color: white;
  }
  .h3{
      color: white;
  }
  .content-footer {
    padding: 2em 0;
    margin-top: 3em;
    background: #2b3840;
  }
  
  a {
    text-decoration: none;
    font-weight: bold;
  }
  
  a:link {
    color: #B69760;
  }
  
  a:visited {
    color: #B69760;
  }
  
  a:hover {
    color: #B69760;
  }
  
  @media only screen and (max-width: 571px) {
      .masthead {
          padding: 5em 0;
      }
      .masthead-intro {
          font-size: 3em;
      }
      .masthead-heading {
          font-size: .5em;
      }
      .container {
          padding: 0 3em;
      }
      .footer-container {
          padding: 0 3em;
    }
  }